.route-content__title {
  @include fz(30px);
  position: relative;
  display: inline-block;
  margin: 0 0 14px;
  color: $c-blue;
  font-family: $f-din-bold;
  letter-spacing: .5px;
  line-height: 24px;
  text-transform: capitalize;

  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: -35px;
    display: block;
    width: 26px;
    height: 26px;
    background-repeat: no-repeat;
    background-position: center;
  }

  &_croatia::after {
    background-image: url('../../../img/route/croatia/flag.jpg');
  }

  &_greece::after {
    background-image: url('../../../img/route/greece/flag.jpg');
  }

  &_italy::after {
    background-image: url('../../../img/route/italy/flag.jpg');
  }

  &_caribbean::after {
    background-image: url('../../../img/route/caribbean/flag.jpg');
  }

  &_portugal::after {
    background-image: url('../../../img/route/portugal/flag.jpg');
  }
}
