.box-link {
  padding: 20px;
  border-radius: 10px;
  background-color: $c-white;
  text-decoration: none;

  @include mq(tablet) {
    padding: 30px;
  }

  &:hover {
    background-color: $c-blue;

    .box-link__title  {
      color: $c-white;
    }

    svg {
      fill: $c-white;
    }

    .box-link__desc {
      color: rgba(255, 255, 255, .5);
    }
  }
}
