.team {
  position: relative;
  padding: 0 20px 30px;
  margin: 0 15px 40px;
  border-radius: 10px;

  &::after {
    content: '';
    position: absolute;
    top: 60px;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: $c-white;
    border-radius: 10px;

    @include mq(tablet) {
      top: 70px;
    }

    @include mq(desktop) {
      top: 100px;
    }
  }

  @include mq(tablet) {
    padding: 0 28px 30px;
  }
}
