.head__col {
  display: flex;
  flex-direction: column;
  justify-content: center;

  @include mq(tablet) {
    width: 50%;
  }

  &:nth-child(1) {
    @include mq(tablet) {
      align-items: flex-start;
    }
  }

  &:nth-child(2) {
    @include mq(tablet, max) {
      margin-top: 40px;
    }

    @include mq(tablet) {
      align-items: flex-end;
    }
  }
}