.head {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0 0 40px;

  @include mq(tablet) {
    flex-direction: row;
    margin: 0 0 50px;
  }

  @include mq(desktop) {
    margin: 0 0 86px;
  }

  &.head_center {
    .head__col {
      align-items: center;
      width: 100%;
    }
  }
}
