.team-banner__text {
  @include fz(15px);
  margin: 0 0 20px;
  color: rgba(255, 255, 255, 0.5);
  font-family: $f-muller-medium;
  letter-spacing: .5px;

  &:last-of-type {
    margin: 0 0 36px;

    @include mq(tablet) {
      margin: 0 0 60px;
    }
  }

  @include mq(tablet) {
    @include fz(16px);
    max-width: 590px;
  }
}
