.review {
  position: relative;
  padding: 0 20px 20px;
  border-radius: 10px;

  @include mq(tablet) {
    padding: 0 30px 20px;
  }

  &::after {
    content: '';
    position: absolute;
    top: 40px;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: $c-white;
    border-radius: 10px;
  }
}