.head__support {
  display: flex;
  align-items: center;
  color: $c-dark;
  text-decoration: none;

  @include mq(tablet) {
    max-width: 330px;
  }

  span {
    @include fz(15px);
    position: relative;
    top: 2px;
    font-family: $f-muller-medium;
    letter-spacing: .5px;
    line-height: 20px;
  }
}