.boat {
  padding: 16px 18px 18px;
  text-decoration: none;
  border-radius: 10px;
  background-color: $c-white;

  @include mq(tablet) {
    padding: 24px 24px 16px;
  }
  
  &:hover {
    background-color: $c-blue;
    
    .boat__name {
      color: $c-white;
    }

    @include mq(tablet) {
      .boat__info {
        background-color: $c-blue-shadow;
      }
    }

    .boat__icon {
      fill: $c-white;
    }

    .boat__count {
      color: $c-white;
    }
  }
}
