.breadcrumbs-list__item {
  position: relative;
  display: flex;
  align-items: center;
  padding-right: 24px;

  &::after {
    content: '';
    position: absolute;
    top: 50%;
    right: 10px;
    width: 3px;
    height: 6px;
    background: url('../img/svg/arrow.svg') no-repeat center;
    background-size: contain;
    transform: translateY(-50%);
  }

  &:last-child {
    &::after {
      display: none;
    }
  }
}
