html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
  font-family: $f-din-bold;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1.25;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  width: 100%;
  padding: 0 20px;

  @include mq(tablet) {
    max-width: 768px;
    margin: 0 auto;
  }

  @include mq(desktop) {
    max-width: 1300px;
    padding: 0;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.header,
.footer {
  flex: 0 0 auto;
}

.main {
  flex: 1 0 auto;
  background-color: $c-main-bg;
}

.center {
  margin: auto;
}

.m-hidden {
  @include mq(tablet, max) {
    display: none;
  }
}

.d-hidden {
  @include mq(tablet) {
    display: none;
  }
}
