// Button variants
// Default styles, as well as :hover, :active,
// and disabled options for all buttons

//  Box model
@mixin box-model {
  display: flex;
  align-items: center;
  justify-content: center;
}

//  Button size
@mixin button-size {
  @each $item, $size in $button-sizes {
    &-#{$item} {
      padding-right: $size;
      padding-left: $size;
    }
  }
}

//  Button default
@mixin button {
  @include button-size;
  @include box-model;

  position: relative;
  padding-top: .813rem;
  padding-bottom: .813rem;
  border: 0;
  cursor: pointer;
  background-color: transparent;
  transition: all .35s;
  outline: none;

  &::before {
    content: "";
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    background: linear-gradient(45deg, #3C589E, #93B7E4);
  }

  &:active {
    padding-left: calc(2rem - 5px);
    padding-right: calc(2rem - 5px);
  }

  &:hover:before {
    background: linear-gradient(45deg, #4D6FB8, #AACFEF);
  }

  &:active:before {
    background: linear-gradient(45deg, #4D6FB8, #AACFEF);
  }
}
