.box-info {
  width: calc(50% - 30px);
  margin: 0 15px 40px;
  text-decoration: none;

  @include mq(tablet) {
    width: calc(50% - 50px);
    margin: 0 25px 80px;
    padding: 0 86px;
    border-left: 2px solid #bbbbbb;
    border-right: 2px solid #bbbbbb;
  }

  @include mq(desktop) {
    margin: 0 30px 56px;
    padding: 0;
    max-width: 164px;
    border-left: none;
    border-right: none;
  }
}
