.button__shadow {
  position: absolute;
  left: 20px;
  right: 20px;
  bottom: 0;

  &:after {
    content: '';
    position: absolute;
    z-index: 1;
    left: 50%;
    bottom: 0;
    width: 100%;
    height: 20px;
    box-shadow: 0 5px 40px rgba(148, 183, 228, 0.6);
    transform: translateX(-50%);
  }
}