.faq-list__item {
  position: relative;

  &::before {
    @include fz(10px);
    counter-increment: step;
    content: counter(step, decimal-leading-zero);
    position: absolute;
    top: 4px;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 22px;
    height: 22px;
    color: $c-white;
    font-family: $f-muller-medium;
    border-radius: 50%;
    background: linear-gradient(-120deg, $c-light-blue, $c-dark-blue);
    box-shadow: 0 10px 35px $c-blue-shadow;

    @include mq(tablet) {
      top: 10px;
    }
  }

  &:not(:last-child) {
    margin: 0 0 50px;
  }
}
