.best-info__title {
  @include fz(30px);
  @extend %common-title;
  position: relative;
  top: -2px;
  text-decoration: none;
  text-transform: capitalize;

  &::after {
    content: '';
    position: absolute;
    top: 5px;
    right: -35px;
    display: block;
    width: 26px;
    height: 26px;
    background-repeat: no-repeat;
    background-position: center;
  }

  &_croatia::after {
    background-image: url('../img/route/croatia/flag.jpg');
  }

  &_greece::after {
    background-image: url('../img/route/greece/flag.jpg');
  }

  &_italy::after {
    background-image: url('../img/route/italy/flag.jpg');
  }

  &_caribbean::after {
    background-image: url('../img/route/caribbean/flag.jpg');
  }

  &_portugal::after {
    background-image: url('../img/route/portugal/flag.jpg');
  }
}