.footer__col_info-wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  p, a:not(.footer__logo) {
    @include fz(12px);
    margin: 0;
    color: rgba(255, 255, 255, .5);
    font-family: $f-muller-medium;
    letter-spacing: .3px;
    text-decoration: none;
  }

  @include mq(tablet) {
    margin: 0 50px 0 0;
    border-right: 1px solid rgba(255, 255, 255, .2);
  }

  @include mq(desktop) {
    margin: 0;
  }
}