.team__name {
  @include fz(26px);
  @extend %common-title;
  position: relative;
  z-index: 1;
  display: block;
  margin: 20px 0 26px 120px;
  letter-spacing: .8px;
  text-decoration: none;

  @include mq(tablet) {
    margin: 10px 0 64px 140px;
  }

  @include mq(desktop) {
    @include fz(30px);
    margin: 34px 0 64px 208px;
  }
}
