.footer-services {
  display: flex;
  justify-content: space-between;
  padding: 20px 0;
  margin: 20px 0;
  border-top: 1px solid rgba(255, 255, 255, .2);
  border-bottom: 1px solid rgba(255, 255, 255, .2);

  @include mq(tablet) {
    flex-grow: 1;
    max-width: 500px;
    padding: 0 2%;
    margin: 0 2%;
    border-left: 1px solid rgba(255, 255, 255, .2);
    border-right: 1px solid rgba(255, 255, 255, .2);
    border-top: none;
    border-bottom: none;
  }

  @include mq(desktop) {
    padding: 28px 0;
    margin: 30px 0;
    border-right: none;
    border-left: none;
    border-top: 1px solid rgba(255, 255, 255, .2);
    border-bottom: 1px solid rgba(255, 255, 255, .2);
  }
}