.footer__row {
  @include mq(tablet) {
    display: flex;
  }

  @include mq(desktop, max) {
    margin: 0 0 20px;
    padding: 0 0 20px;
    border-bottom: 1px solid rgba(255, 255, 255, .2);

    &:last-child {
      margin: 0;
      padding: 0;
      border-bottom: none;
    }
  }

  @include mq(desktop) {
    width: 25%;

    &:first-child {
      width: 50%;
    }

    &:last-child {
      justify-content: flex-end;
    }
  }
}