.footer-form__field {
  @include fz(14px);
  width: 100%;
  height: 50px;
  padding: 0 150px 0 50px;
  color: $c-white;
  font-family: $f-muller-medium;
  letter-spacing: .3px;
  border: 1px solid #38608C;
  border-radius: 25px;
  background-color: transparent;

  @include mq(tablet) {
    padding: 0 150px 0 66px;
  }

  @include mq(desktop) {
    margin: 0 0 34px;
    padding: 0 50px;
    text-align: center;
  }

  &::-webkit-input-placeholder {
    @include fz(14px);
    color: rgba(255,255,255, .5);
    font-family: $f-muller-medium;
    letter-spacing: .3px;
  }
  &::-moz-placeholder {
    @include fz(14px);
    color: rgba(255,255,255, .5);
    font-family: $f-muller-medium;
    letter-spacing: .3px;
  }
  &:-ms-input-placeholder {
    @include fz(14px);
    color: rgba(255,255,255, .5);
    font-family: $f-muller-medium;
    letter-spacing: .3px;
  }
  &:-moz-placeholder {
    @include fz(14px);
    color: rgba(255,255,255, .5);
    font-family: $f-muller-medium;
    letter-spacing: .3px;
  }
}