.head-form__field {
  @include fz(18px);
  width: 100%;
  padding: 6px 30px 6px 0;
  color: $c-dark;
  font-family: $f-muller-medium;
  letter-spacing: .3px;
  border: none;
  border-bottom: 1px solid $c-border;
  background-color: transparent;
  outline: none;

  @include mq(desktop) {
    width: 336px;
  }

  &::-webkit-input-placeholder {
    @include fz(14px);
    color: $c-field-default;
    font-family: $f-muller-medium;
    letter-spacing: .3px;
  }
  &::-moz-placeholder {
    @include fz(14px);
    color: $c-field-default;
    font-family: $f-muller-medium;
    letter-spacing: .3px;
  }
  &:-ms-input-placeholder {
    @include fz(14px);
    color: $c-field-default;
    font-family: $f-muller-medium;
    letter-spacing: .3px;
  }
  &:-moz-placeholder {
    @include fz(14px);
    color: $c-field-default;
    font-family: $f-muller-medium;
    letter-spacing: .3px;
  }
}