.layout__col {
  &:first-child {
    width: 100%;

    @include mq(tablet) {
      width: 50%;
    }

    @include mq(desktop) {
      width: 480px;
    }
  }

  &:last-child {
    width: 100%;

    @include mq(tablet) {
      width: 50%;
    }

    @include mq(desktop) {
      width: 820px;
    }
  }
}
