.team__position {
  @include fz(15px);
  position: relative;
  z-index: 1;
  margin: 0 0 30px 120px;
  color: $c-blue;
  font-family: $f-muller-medium;
  letter-spacing: .3px;

  @include mq(tablet) {
    @include fz(16px);
    margin: 0 0 34px 140px;
  }

  @include mq(desktop) {
    margin: 0 0 34px 208px;
  }
}
