%avatar {
  position: relative;
  z-index: 1;
  float: left;

  &:after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    width: 68px;
    height: 68px;
    box-shadow: 0 15px 55px rgba(148, 183, 228, .6);
    transform: translateX(-50%);
  }

  picture {
    position: relative;
    z-index: 2;
    display: block;
    border-radius: 10px;
    overflow: hidden;
  }
}